html,
body {
  max-width: 100%;
  overflow-x: hidden;
}

aside {
  z-index: 4;
  position: fixed;
  top: 0;
  bottom: 0;
  background: #fff;
  color: #000;
  min-width: 250px;
  max-width: 250px;
  margin-top: 1rem;
  margin-bottom: 1rem;
  margin-left: 1rem;
  padding-top: 1rem;
  padding-left: 0;
  border: 1px solid #e9ecef;
  border-radius: 16px;
  transition: all 0.3s;
  overflow-y: auto;
  /* Mainstream tablet */
}
aside .sidebar-nav {
  padding: 0 1em;
}
aside .sidebar-nav li {
  list-style: none;
  text-indent: 1em;
  line-height: 2.5rem;
  margin-bottom: 0.25rem;
}
aside .sidebar-nav li:hover {
  color: #E8AA42;
  border-radius: 8px;
}
aside .sidebar-nav li a {
  display: block;
  text-decoration: none;
  color: #000;
}
aside .sidebar-nav li.active a.active {
  border-radius: 8px;
  background-color: #231955;
  color: #fff;
}
@media (max-width: 767px) {
  aside {
    margin-left: -250px;
    position: fixed;
    min-height: 100vh;
    z-index: 1;
  }
}

.content {
  max-width: 100%;
  overflow-x: hidden;
  min-height: 100vh;
  width: 100%;
  padding: 4.5rem 0 0 0;
  margin-left: 264px;
  margin-right: 0;
}
@media (max-width: 767px) {
  .content {
    margin-left: 0;
  }
}
.content .content-section {
  position: fixed;
  top: 5rem;
  left: 272px;
  right: 0;
  overflow-y: auto;
  height: 100vh;
  padding-bottom: 5rem;
}
@media (max-width: 767px) {
  .content .content-section {
    left: 0;
  }
}
.content .table-responsive,
.content table {
  margin-bottom: 0;
}
.content .pagination {
  margin: 0;
}
.content .card .card-header {
  background-color: #fff;
}
.content .card table {
  margin-bottom: 0;
}
.content .card .card-footer {
  background-color: #fff;
  border-bottom: 1px solid #dee2e6;
  border-left: 1px solid #dee2e6;
  border-right: 1px solid #dee2e6;
}
.content .card .card-footer .pagination {
  margin: 0;
}
.content .navbar {
  left: 264px;
  right: 0;
  margin-top: 1rem;
  margin-left: 1.5rem;
  margin-right: 1rem;
  border-radius: 16px;
  background: #fff;
  /* Mainstream tablet */
}
@media (max-width: 767px) {
  .content .navbar {
    margin-left: -250px;
  }
}
.content .navbar .navbar-brand {
  /* Mainstream tablet */
}
@media (max-width: 767px) {
  .content .navbar .navbar-brand {
    width: 3em;
  }
}
.content .navbar .navbar-brand .brand-wrapper a {
  color: #000;
}
.content .navbar .search-navbar .select2-selection .select2-selection--single {
  border-radius: 16px;
}
.content .navbar .search-navbar .select2-container--krajee-bs5 .select2-selection {
  background-color: transparent;
  border-radius: 16px;
}
.content .navbar .search-navbar .select2-container--krajee-bs5 .select2-selection--single .select2-selection__rendered {
  color: #212529;
  padding-left: 8px;
  padding-right: 8px;
}
.content .navbar .navbar-photo-profile img {
  width: 92px;
  height: auto;
}
.content .navbar .navbar-photo-profile span {
  text-align: center;
}
.content .site-account-information .photo-profile img {
  width: 9rem;
  height: auto;
}
.content .site-account-information .photo-profile span {
  font-size: 1.35em;
  text-align: center;
}

footer {
  margin: 1rem 0;
  font-size: 0.9em;
}

body.dark {
  background-color: #212529 !important;
  color: #fff !important;
}
body.dark a {
  color: #fff;
}
body.dark input.form-control,
body.dark select.form-control,
body.dark textarea,
body.dark table,
body.dark table th,
body.dark table td,
body.dark div.card,
body.dark div.card .card-header,
body.dark div.card .card-footer,
body.dark aside,
body.dark nav.card,
body.dark ul.pagination li.page-item a.page-link,
body.dark footer {
  background-color: #212529 !important;
  color: #fff !important;
}
body.dark aside {
  border: 1px solid #fff;
  box-shadow: none;
}
body.dark .navbar,
body.dark body.dark nav.card {
  box-shadow: 0 0.5rem 1rem rgba(255, 255, 255, 0.15) !important;
}
body.dark .navbar .brand-wrapper a {
  color: #fff !important;
}
body.dark ul.pagination li.page-item.active a.page-link {
  color: #212529 !important;
  background-color: #fff !important;
  border-color: transparent;
}
body.dark div.card {
  border: 1px solid #fff;
}
body.dark span.navbar-brand a,
body.dark aside.sidebar li a,
body.dark header .bi.bi-list {
  color: #fff;
}
body.dark aside.sidebar li a {
  color: #fff;
}
body.dark aside.sidebar li.active a {
  color: #fff;
}
body.dark .dropdown-menu {
  background-color: #212529 !important;
  color: white;
}

.not-set {
  color: #c55;
  font-style: italic;
}

/* add sorting icons to gridview sort links */
a.asc:after,
a.desc:after {
  content: "";
  left: 3px;
  display: inline-block;
  width: 0;
  height: 0;
  border: solid 5px transparent;
  margin: 4px 4px 2px 4px;
  background: transparent;
}

a.asc:after {
  border-bottom: solid 7px #212529;
  border-top-width: 0;
}

a.desc:after {
  border-top: solid 7px #212529;
  border-bottom-width: 0;
}

.grid-view th {
  white-space: nowrap;
}

.hint-block {
  display: block;
  margin-top: 5px;
  color: #999;
}

.error-summary {
  color: #a94442;
  background: #fdf7f7;
  border-left: 3px solid #eed3d7;
  padding: 10px 20px;
  margin: 0 0 15px 0;
}

.user-photo-32 {
  display: inline-block;
  box-shadow: 0 0.46875rem 2.1875rem rgba(90, 97, 105, 0.1), 0 0.9375rem 1.40625rem rgba(90, 97, 105, 0.1), 0 0.25rem 0.53125rem rgba(90, 97, 105, 0.12), 0 0.125rem 0.1875rem rgba(90, 97, 105, 0.1);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
}

.user-photo-64 {
  display: inline-block;
  box-shadow: 0 0.46875rem 2.1875rem rgba(90, 97, 105, 0.1), 0 0.9375rem 1.40625rem rgba(90, 97, 105, 0.1), 0 0.25rem 0.53125rem rgba(90, 97, 105, 0.12), 0 0.125rem 0.1875rem rgba(90, 97, 105, 0.1);
  width: 64px;
  height: 64px;
  border-radius: 50%;
  object-fit: cover;
}

button.navbar-toggler {
  border: 0;
}

.btn-toggle {
  display: inline-flex;
  align-items: center;
  padding: 0.25rem 0.5rem;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.65);
  background-color: transparent;
  border: 0;
}

.btn-toggle:hover,
.btn-toggle:focus {
  color: rgba(0, 0, 0, 0.85);
  background-color: #d2f4ea;
}

.btn-toggle::before {
  width: 1.25em;
  line-height: 0;
  content: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='16' height='16' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='rgba%280,0,0,.5%29' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M5 14l6-6-6-6'/%3e%3c/svg%3e");
  transition: transform 0.35s ease;
  transform-origin: 0.5em 50%;
}

.btn-toggle[aria-expanded=true] {
  color: rgba(0, 0, 0, 0.85);
}

.btn-toggle[aria-expanded=true]::before {
  transform: rotate(90deg);
}

.btn-toggle-nav a {
  display: inline-flex;
  padding: 0.1875rem 0.5rem;
  margin-top: 0.125rem;
  margin-left: 1.25rem;
  text-decoration: none;
}

.btn-toggle-nav a:hover,
.btn-toggle-nav a:focus {
  background-color: #d2f4ea;
}

.dropdown-menu.dropdown-menu-end {
  border: 1px #231955 solid;
  border-radius: 16px;
  margin-top: 1rem;
  margin-right: 0;
}

.form-control {
  color: #000;
  background-color: #fff;
}